import React from 'react';
import get from 'lodash/get';
import { createStructuredSelector } from 'reselect';
import Table from '@material-ui/core/Table';
import { Text } from '@sitecore-jss/sitecore-jss-react';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '../../../components/atoms/Typography';
import Grid from '../../../components/molecules/Grid';
import { COMPONENT_NAME } from './locators';
import useStyles from './style.js';
import { connect } from 'react-redux';
import { getSelectedTemperature } from '../../../components/organism/LiveWeatherInfo/redux/selectors';
import { defaultTemperature } from '../../../../constants';
// import { trendTableValues } from './data';

const WeatherTrend = props => {
  const trendTableValues = get(props, 'fields.trendTableValues', []);
  const classes = useStyles();
  const isTemperatureInCel = props.selectedTemperature === defaultTemperature;

  return (
    <Grid className={classes.root} data-locator={COMPONENT_NAME}>
      <Grid container spacing={0}>
        <Grid item xs={12}>
          <TableContainer component={Paper} elevation={0} square>
            <Table className="table">
              <TableHead>
                <TableRow className={`${classes.headingRow} ${classes.row}`}>
                  {trendTableValues.map(weatherTrend => {
                    const { id, fields } = weatherTrend;
                    return get(fields, 'trendTableHeadingC.value', '') && (
                      <TableCell
                        component="th"
                        scope="row"
                        key={id}
                        data-column={isTemperatureInCel ? fields.trendTableHeadingC.value : fields.trendTableHeadingF.value}
                      >
                        <Typography variant="body1Bold">
                          <Text field={isTemperatureInCel ? fields.trendTableHeadingC : fields.trendTableHeadingF} />
                        </Typography>
                      </TableCell>
                    )
                  })}
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow className={classes.row}>
                  {trendTableValues.map(weatherTrend => {
                    const { id, fields } = weatherTrend;
                    return get(fields, 'trendTableHeadingC.value', '') && (
                      <TableCell
                        key={`${id}_high`}
                        data-column={isTemperatureInCel ? fields.trendTableHeadingC.value : fields.trendTableHeadingF.value}
                      >
                        <Typography variant="body1">
                          <Text field={isTemperatureInCel ? fields.trendTableHighRowC : fields.trendTableHighRowF} />
                        </Typography>
                      </TableCell>
                    )
                  })}
                </TableRow>
                <TableRow className={classes.row}>
                  {trendTableValues.map(weatherTrend => {
                    const { id, fields } = weatherTrend;
                    return get(fields, 'trendTableHeadingC.value', '') && (
                      <TableCell
                        key={`${id}_low`}
                        data-column={isTemperatureInCel ? fields.trendTableHeadingC.value : fields.trendTableHeadingF.value}
                      >
                        <Typography variant="body1">
                          <Text field={isTemperatureInCel ? fields.trendTableLowRowC : fields.trendTableLowRowF} />
                        </Typography>
                      </TableCell>
                    )
                  })}
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    </Grid>
  )
}

const mapStateToProps = createStructuredSelector({
  selectedTemperature: getSelectedTemperature,
});

export default connect(mapStateToProps)(WeatherTrend);

export { WeatherTrend as WeatherTrendInfo };
