import makeStyles from '@material-ui/core/styles/makeStyles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const tableBorder = '1px solid rgba(151, 151, 151, 0.29)';
const boxShadow = '0px 2px 4px 0px rgba(0, 0, 0, 0.05)';
const useStyles = makeStyles(theme => ({
  root: {
    ...theme.shape.componentSpacing,
    boxShadow,
    '& table': {
      '& td, & th': {
        padding: pxToRem(15),
        [theme.breakpoints.up('lg')]: {
          padding: `${pxToRem(32)} ${pxToRem(16)}`,
          textAlign: theme.mixins.textLeft,
          '&:first-child': {
            ...theme.mixins.paddingLeft(pxToRem(32)),
          },
        },
      },
    },
    [theme.breakpoints.down('md')]: {
      '& table': {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',

        '& tbody': {
          display: 'flex',
          flexDirection: 'column',
          '& tr': {
            display: 'flex',
            flexDirection: 'column',

            '& td p': {
              ...theme.mixins.paddingLeft('80%'),
            },

            '&:last-child': {
              '& td': {
                '&:first-child': {
                  height: pxToRem(57),
                  padding: 0,
                  '&:before': {
                    content: '""',
                  },
                  '& p': {
                    position: 'relative',
                    border: 0,
                    top: pxToRem(16),
                    paddingTop: 0,
                  },
                },
              },
            },
          },
        },
      },
      '& thead tr': {
        position: 'absolute',
        top: '-9999px',
        left: '-9999px',
      },
      '& tr': { border: tableBorder },
      '& td:before': {
        position: 'absolute',
        ...theme.mixins.left(pxToRem(6)),
        width: 'auto',
        top: pxToRem(14),
        ...theme.mixins.paddingLeft(pxToRem(25)),
        whiteSpace: 'nowrap',
        content: 'attr(data-column)',
      },

      '& td': {
        position: 'relative',
        ...theme.mixins.paddingLeft('50%'),

        '&:last-child': {
          borderBottom: 0,
        },

        '&:first-child': {
          background: theme.palette.background.default,
          height: pxToRem(114),

          '& p': {
            position: 'absolute',
            left: 0,
            whiteSpace: 'nowrap',
            paddingTop: pxToRem(15),
            paddingBottom: pxToRem(17),
            top: pxToRem(57),
            borderTop: tableBorder,
            width: '100%',
            textAlign: theme.mixins.textLeft,
            ...theme.mixins.paddingLeft(pxToRem(32)),
            boxShadow,
          },
        },
      },
    },
  },
  row: {
    border: tableBorder,
    boxShadow,
  },
  headingRow: {
    background: theme.palette.background.default,
    boxShadow,
  },

}));

export default useStyles;
